body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-green: #00F021;
  --main-blue: #00136D;
  --main-yellow: #FFD800;
}
.grid-container-mobile {
  display: grid;
  grid-template-areas:
    'title title title title title title'
    'yellow-bar yellow-bar yellow-bar yellow-bar yellow-bar yellow-bar'
    'sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa'
    'yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two'
    'short-bio short-bio short-bio short-bio short-bio short-bio'
    'social social social social social social'
    'yellow-bar-three yellow-bar-three yellow-bar-three yellow-bar-three yellow-bar-three yellow-bar-three'
    'articles-button articles-button articles-button articles-button articles-button articles-button'
    'cow-logo cow-logo cow-logo cow-logo cow-logo cow-logo';
  grid-gap: 20px;
  padding: 30px;
  font-family: 'Special Elite', cursive;
}

.grid-container-mobile > div {
  text-align: center;
}

.title-mobile { 
  grid-area: title;
  color: white;
  font-size: 30px;
}

.title-left-align-mobile{
  display: inline-block;
  text-align: left;
}

.yellow-bar-mobile{
  grid-area: yellow-bar;
  height: 15px;
  background-color: #FFD800;
  background-color: var(--main-yellow);
}

.sam-for-iowa-mobile{
  grid-area: sam-for-iowa;
  width: 100%;
}

.sam-for-iowa-mobile > img{
  width: 100%;
  height: auto;
}

.yellow-bar-two-mobile{
  grid-area: yellow-bar-two;
  height: 15px;
  background-color: #FFD800;
  background-color: var(--main-yellow);
}

.short-bio-mobile{
  grid-area: short-bio;
  color: white;
  font-size: 20px;
}

.social-mobile{
  grid-area: social;
  color: #00136D;
  color: var(--main-blue);
  background-color: #00F021;
  background-color: var(--main-green);
  font-size: 20px;
  border: 5px solid #FFD800;
  border: 5px solid var(--main-yellow);
}

.yellow-bar-three-mobile{
  grid-area: yellow-bar-three;
  height: 15px;
  background-color: #FFD800;
  background-color: var(--main-yellow);
}

.articles-button-mobile{
  grid-area: articles-button;
  height: 100px;
  font-size: 30px;
  padding-top: 20px;
  padding-bottom: 40px;
  margin-bottom: 0px;
}

.button-inline-mobile{
  display: inline-block;
  background-color: #FFD800;
  background-color: var(--main-yellow);
  padding: 20px;
  width: 70%;
  border: 5px solid #00F021;
  border: 5px solid var(--main-green);
}

.cow-logo-mobile{
  grid-area: cow-logo;
  width: 100%;
}

.cow-logo-mobile > img{
  width: 100%;
  height: auto;
}

a, a:visited{
  color: #00136D;
  color: var(--main-blue);
}






.header-image{
  width: 100%;
}

.header-image > img{
  width: 100%;
  height: auto;
  object-fit: cover;
}

.topbar{
  height: 40px;
  text-align: right;
  width: 100%;
}

.topbar-buttons{
  height: 100%;
  background-color: var(--main-blue);
  display: inline-block;
}

.topbar-button-desktop{
  padding-right: 40px;
  padding-left: 40px;
  padding-top: 10px;
  height: 100%;
  display: inline-block;
  font-weight: bold;
}

.topbar-button-mobile{
  padding-right: 10px;
  padding-left: 20px;
  padding-top: 10px;
  height: 100%;
  display: inline-block;
  font-weight: bold;
}

.topbar-button-desktop > a{
  text-decoration: none;
  color: white;
}

.topbar-button-desktop > a:visited{
  text-decoration: none;
  color: white;
}

.topbar-button-mobile > a{
  text-decoration: none;
  color: white;
}

.topbar-button-mobile > a:visited{
  text-decoration: none;
  color: white;
}
